import React, { Component } from 'react';
import {Prompt} from 'react-router-dom';

class HomeB extends Component {
    constructor(){
        super()
        this.state={
            enter:"!"
        }
    }
    onHandleChange(event){
       this.setState({
         enter : event.target.value
       })
       console.log("dd:",event.target.value)
    }
    onHandleClick(){
        this.setState({
            enter : ""
          })
    }

      /*  ,  */
      componentWillMount() {
        console.log("Component will mount");
      }
       /* DOMthis.getDOMNode() JavaScriptsetTimeout, setIntervalAJAX(UI) */
      componentDidMount() {
        console.log("Component did mount");
      }
    /*  prop ()render */
      componentWillReceiveProps(nextProps) {
        console.log('Component will receive props', nextProps);
      }
    /* propsstateforceUpdate  */
      shouldComponentUpdate(nextProps, nextState) {
        console.log('Component should update', nextProps, nextState);
        if (nextState.status === 1) {
          return false;
        }
        return true;
      }
    /* propsstaterender */
      componentWillUpdate(nextProps, nextState) {
        console.log('Component will update', nextProps, nextState);
      }
    /*  */
      componentDidUpdate(prevProps, prevState) {
        console.log('Component did update', prevProps, prevState);
      }
    /*  DOM  */
      componentWillUnmount() {
        console.log('Component will unmount');
      }
    render() {
        return (
            <div className="HomeB">
                <h1>HomeB</h1>
                <Prompt message=""/>
                <label>
                   <input type="text" value={this.state.enter} onChange={(event)=>this.onHandleChange(event)}/>
                   <button onClick={()=>this.onHandleClick()}></button>
                </label>
                
                <p>{this.state.enter}</p>
            </div>
        )}
}
export default HomeB;


